.mainMaterial {
    position: relative;
    display: flex;
    flex-direction: row;
    font-family: 'Quicksand', sans-serif;
    width: 100%;
    height: 60px;
    top: 3rem;
  }

.buttonDiv {
  margin-right: auto;
  margin-left: auto;
}

.buttonMaterial {
  margin-right: 2rem;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  background-color: #5369cc;
  color: #ffffff;
  font-size: 20px;
  font-weight: 600;
  transition: background-color .5s;
  width: 10rem;
}

.buttonMaterial:hover {
  cursor: pointer;
  background-color: #1d63e6;
}
