/* Universal Styles */

@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700&display=swap');

html {
  font-size: 18px;
  background-color: #537dcc;
}

@media only screen and (max-width: 560px) {
  html {
    font-size: 15px;
  }
}

input:focus {
  outline: none;
}

/* Reusable Component - Image Container */

.image-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  background: #000;
}

.image-container img {
  width: 100%;
}

/* App Styles */

.App {
  font-family: 'Rubik', sans-serif;
}

body {
  color: white;
}

h1 {
  padding: .66rem 0;
  padding-bottom: 1rem;
  text-align: center;
  background-color: #537dcc;
  font-family: 'Quicksand', sans-serif;
  font-size: 2.7rem;
  font-weight: 550;
  color: #fff;
}

.pageTitle {
  border-bottom: 3px solid white;
}
