.advancedMain {
  margin-top: 1rem;
  margin-left: 1rem;
  width: 100%;
  height: 50rem;
}

.advancedButtonContainer {
    margin-top: 10rem;
    margin-right: auto;
    margin-left: auto;
  }
  
  .advancedButton {
    margin-right: 10rem;
    border-radius: 4px;
    padding: 0.7rem 1.5rem;
    background-color: #5369cc;
    font-size: 16px;
    font-weight: 600;
    transition: background-color .5s;
  }
  
  .advancedButton:hover {
    cursor: pointer;
    background-color: #1d63e6;
  }
  
  .divButton {
    margin-right: auto;
    margin-left: auto;
  }