.SearchPage {
  background-image: url('./forside.jpg');
  height: 51.5rem;
}

.SearchBar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* background-image: url('./concrete-texture-bg.jpg'); */
  background-color: #5ea9f0dc;
  background-size: cover;
  background-repeat: no-repeat;
  height: 9rem;
  border-bottom: 3px solid white;
}

.SearchBar-sort-options ul {
  display: flex;
  justify-content: center;
  margin-bottom: 2.22rem;
  color: #ffffff;
}

.SearchBar-sort-options li {
  cursor: pointer;
  width: 4.33rem;
  border-bottom: 1px solid #fff;
  padding: 0 2.58rem .33rem 2.58rem;
  line-height: 1.13;
  text-align: center;
  font-weight: 600;
  font-size: .83rem;
  transition: color .25s;
}

.SearchBar-sort-options li:hover {
  color: #d4cfcf;
}

.SearchBar-sort-options li.active,
.SearchBar-sort-options li.active:hover {
  border-bottom: 1px solid #537dcc;
  color: #537dcc;
}

.SearchHeader {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #282c34;
}

.SearchBar-submit {
  text-align: center;
  margin-top: 25px;
}

.SearchBar-submit a {
  border-radius: 4px;
  padding: .72rem 1.7rem;
  background-color: #537dcc;
  color: #ffffff;
  font-weight: 600;
  transition: background-color .5s;
}

.SearchBar-submit a:hover {
  cursor: pointer;
  background-color: #1d63e6;
}

@media only screen and (max-width: 560px) {
  .SearchBar {
    background-image: url('./concrete-texture-bg.jpg');
  }

.SearchBar-sort-options ul {
    margin-left: 2rem;
    margin-right: 2rem;
  }

.SearchBar-fields {
    flex-direction: column;
    align-items: center;
  }

.SearchBar-fields input {
    margin-right: 0;
    margin-bottom: .86rem;
  }
}