.Building {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 600px;
}

.Building .image-container {
  display: block;
  margin-left: 1rem;
  margin-bottom: 1rem;
  margin-top: 1rem;
  width: 550px;
  height: 550px;
  border-style: ridge;
  border-width: 5px;
  border-color: white;
}

.address-title {
  position: absolute;
  left: 32rem;
  margin-top: 0.8rem;
  margin-left: 1rem;
  font-size: 1.5rem;
  font-weight: 500;
}

.Building .information-container {
  position: absolute;
  display: flex;
  flex-direction: row;
  left: 32rem;
  margin-top: 0.8rem;
  margin-left: 1rem;
  margin-top: 3rem;
}

.address-information {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 200px;
}

.address-information p {
  font-size: 20px;
  margin-bottom: 0.85rem;
  display: flex;
  justify-content: left;
  border-bottom: solid;
  border-width: 2px;
  padding-bottom: 0.3rem;
}

.address-values {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: right;
}

.address-values p {
  font-size: 20px;
  margin-bottom: 0.85rem;
  display: flex;
  justify-content: right;
  border-bottom: solid;
  border-width: 2px;
  width: 45px;
  padding-bottom: 0.3rem;
  font-weight: 450;
}

#add2 {
  margin-left: 1rem;
}

.imageStage {
  margin-left: 5rem;
}

