.simpleMain {
  margin-top: 1rem;
  margin-left: 1rem;
  width: 100%;
  height: 50rem;
}

.simpleInfo {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.dropdownContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

.dropdownMenu {
  width: 20%;
  margin-left: 1rem;
}

.dropdownTitle {
  margin-bottom: 0.5rem;
  font-weight: 500;
}

.chartjs-render-monitor {
  background-color: white;
  height: 140px;
}

.simpleButtonContainer {
    position: relative;
    top: 5rem;
    margin-right: auto;
    margin-left: auto;
}
  
.simpleButton {
  margin-right: 10rem;
  border-radius: 4px;
  padding: 0.7rem 1.5rem;
  background-color: #5369cc;
  color: #ffffff;
  font-size: 16px;
  font-weight: 600;
  transition: background-color .5s;
}
  
.simpleButton:hover {
  cursor: pointer;
  background-color: #1d63e6;
}