@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/* Universal Styles */

html {
  font-size: 18px;
  background-color: #537dcc;
}

@media only screen and (max-width: 560px) {
  html {
    font-size: 15px;
  }
}

input:focus {
  outline: none;
}

/* Reusable Component - Image Container */

.image-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  background: #000;
}

.image-container img {
  width: 100%;
}

/* App Styles */

.App {
  font-family: 'Rubik', sans-serif;
}

body {
  color: white;
}

h1 {
  padding: .66rem 0;
  padding-bottom: 1rem;
  text-align: center;
  background-color: #537dcc;
  font-family: 'Quicksand', sans-serif;
  font-size: 2.7rem;
  font-weight: 550;
  color: #fff;
}

.pageTitle {
  border-bottom: 3px solid white;
}

.SearchPage {
  background-image: url(/bygningspas/static/media/forside.d5ced67f.jpg);
  height: 51.5rem;
}

.SearchBar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* background-image: url('./concrete-texture-bg.jpg'); */
  background-color: #5ea9f0dc;
  background-size: cover;
  background-repeat: no-repeat;
  height: 9rem;
  border-bottom: 3px solid white;
}

.SearchBar-sort-options ul {
  display: flex;
  justify-content: center;
  margin-bottom: 2.22rem;
  color: #ffffff;
}

.SearchBar-sort-options li {
  cursor: pointer;
  width: 4.33rem;
  border-bottom: 1px solid #fff;
  padding: 0 2.58rem .33rem 2.58rem;
  line-height: 1.13;
  text-align: center;
  font-weight: 600;
  font-size: .83rem;
  transition: color .25s;
}

.SearchBar-sort-options li:hover {
  color: #d4cfcf;
}

.SearchBar-sort-options li.active,
.SearchBar-sort-options li.active:hover {
  border-bottom: 1px solid #537dcc;
  color: #537dcc;
}

.SearchHeader {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #282c34;
}

.SearchBar-submit {
  text-align: center;
  margin-top: 25px;
}

.SearchBar-submit a {
  border-radius: 4px;
  padding: .72rem 1.7rem;
  background-color: #537dcc;
  color: #ffffff;
  font-weight: 600;
  transition: background-color .5s;
}

.SearchBar-submit a:hover {
  cursor: pointer;
  background-color: #1d63e6;
}

@media only screen and (max-width: 560px) {
  .SearchBar {
    background-image: url(/bygningspas/static/media/concrete-texture-bg.95396712.jpg);
  }

.SearchBar-sort-options ul {
    margin-left: 2rem;
    margin-right: 2rem;
  }

.SearchBar-fields {
    flex-direction: column;
    align-items: center;
  }

.SearchBar-fields input {
    margin-right: 0;
    margin-bottom: .86rem;
  }
}
.Building {
